<script>
import defaultUrl from '@/static/images/default-stadium.png'
import share from '@/static/images/share.png'
import collectFill from '@/static/images/collect-fill.png'
import collect from '@/static/images/collect.png'
import navBar from '@/components/navBar/index.vue'
import { getWxSiteDetail, addWxCollect } from '@/apis/common.js'
import { getWxStadium } from '@/apis/stadium.js'
import bottomButton from '@/components/bottomButton.vue'
import stadiumReserve from '@/mixins/stadiumReserve.js'
import themeMixins from '@/mixins/themeMixins.js'
// import uniStorage from "@/utils/storages/uniStorage";
export default {
  mixins: [stadiumReserve, themeMixins],
  components: {
    navBar,
    bottomButton,
  },
  data() {
    return {
      share,
      collectFill,
      collect,
      info: {},
      form: {},
      isCollect: false,
    }
  },
  computed: {
    buttonTitle() {
      if (
        this.info.service_status == 1 &&
        this.info.service_provider !== 0 &&
        this.info.service_type.includes('stadium_order')
      ) {
        return '预订'
      } else {
        return '电话咨询'
      }
    },
  },
  onUnload() {},
  onLoad(options) {
    if (options.stadium_id) {
      let form = {
        stadium_id: options.stadium_id,
      }
      this.form = { ...form }
      this.getDetail()
    }
  },
  methods: {
    toLocation() {
      this.$toLocation(this.info)
    },
    toPhone(info) {
      if (info.service_status == 1 && info.service_provider !== 0) {
        this.handleReserve(this.info)
      } else {
        this.$callPhone(this.info)
      }
    },
    toCollect() {
      uni.$uv.throttle(async () => {
        let flag = await getApp().judgeIsAuth()
        if (!flag) {
          return
        }
        // this.starStatus = this.info.is_collect ? 2 : 1;
        // if (this.info.is_collect == 1) {
        //   uni.showToast({
        //     type: "fail",
        //     title: "取消收藏",
        //   });
        //   this.isCollect = false;
        // } else {
        //   uni.showToast({
        //     type: "success",
        //     title: "收藏成功",
        //   });
        //   this.isCollect = true;
        // }

        let isCollect = true
        if (this.info.is_collect == 1) {
          isCollect = false
        }
        let params = {
          type_id: 3,
          type: 2,
          // small_type_id: this.info.type_id,
          topic_id: this.form.stadium_id,
          // status: this.isCollect ? 1 : 2,
          status: isCollect ? 1 : 2,
        }
        let res = await addWxCollect(params)
        if (res.code == 200) {
          this.starStatus = this.info.is_collect ? 2 : 1
          if (this.info.is_collect == 1) {
            uni.showToast({
              type: 'fail',
              title: '取消收藏',
            })
            this.isCollect = false
            this.info.is_collect = 2
          } else {
            uni.showToast({
              type: 'success',
              title: '收藏成功',
            })
            this.isCollect = true
            this.info.is_collect = 1
          }
        } else {
          uni.showToast({
            icon: 'none',
            title: res.message,
          })
        }
      }, 1000)
    },
    async getDetail() {
      let res = await getWxStadium(this.form)
      if (res.code == 200) {
        res.data.des = this.$formatRichText(res.data.des, '暂无内容')
        if (res.data.images_url && res.data.images_url.length == 0) {
          res.data.images_url = [defaultUrl]
        }
        this.info = res.data
        this.isCollect = res.data.is_collect == 1 ? true : false
      }
    },
    apply(form) {
      uniStorage.set('APPLYFORM', {
        trainId: form.train_activity_id,
        title: form.promote_name,
      })
      uni.navigateTo({
        url: `/pages/trainApply/index`,
      })
    },
  },
}
</script>
